.sign__button {
    background-color: #fff;
    color: #000;
    font-size: 18px;
    font-weight: 400;
    line-height: 22px;
    text-align: center;
    cursor: pointer;
    margin: 250px 0 15px;
    padding: 0;
    width: 358px;
    height: 50px;
    border-radius: 2px;
    border: none;
    outline: none;
}

.sign__button:hover {
    opacity: .6;
}

@media screen and (max-width: 620px) {
    .sign__button{
        font-size: 16px;
        line-height: 19px;
        margin: 170px 0 20px;
        width: 260px;
        height: 46px;
    }
}