.sign__text {
    margin: 0 0 30px;
    padding: 0;
    font-size: 18px;
    line-height: 22px;
    font-weight: 400;
    color: #fff;
    cursor: pointer;
}

.sign__text:last-of-type:hover {
    opacity: .6;
}

@media screen and (max-width: 620px) {
    .sign__text{
        font-size: 14px;
        line-height: 17px;
    }
}